* {
  font-family: Arial, Helvetica, sans-serif, Charcoal, sans-serif;
}

html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.wrapper {
    background: rgb(122, 117, 117);
    padding: 5px;
    padding-top: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    height: 100%;
    overflow: auto;
  }
  
.header {
    color: rgb(16, 17, 16);
    font-size: 4vh;
    margin-bottom: 15px;
    width: 100%;
  }
  
  .wins-losses {
    float: right;
  }
  
  .gameName {
    float: left;
  }
  
.pictures {
    position: relative;
    background: white (238, 235, 235, 0);
    height: 250px;
    margin: 7px;
    width: 20%;
  }
  
  .pictures > .picture-holder {
    background: #0712ac;
    height: 75%;
    overflow: hidden;
    text-align: center;
  }
  .pictures > .picture-holder > img {
    width: 50%;
  }
